<template>
  <view class="custom-tab-bar">
    <view 
      v-for="(item, index) in tabs" 
      :key="index" 
      class="tab-item"
      :class="{ active: currentTab === item.pagePath }"
      @click="switchTab(item.pagePath)"
    >
      <view class="tab-icon">
        <image :src="currentTab === item.pagePath ? item.selectedIconPath : item.iconPath" mode="aspectFit"></image>
      </view>
      <text class="tab-text">{{ item.text }}</text>
    </view>
  </view>
</template>

<script>
export default {
  name: 'TabBar',
  data() {
    return {
      currentTab: '/pages/index/index',
      tabs: [
        {
          pagePath: '/pages/index/index',
          iconPath: '/static/images/home.png',
          selectedIconPath: '/static/images/home-active.png',
          text: '首页'
        },
        {
          pagePath: '/pages/stress/index',
          iconPath: '/static/images/stress.png',
          selectedIconPath: '/static/images/stress-active.png',
          text: '压力检测'
        },
        {
          pagePath: '/pages/intervene/intervene',
          iconPath: '/static/images/intervene.png',
          selectedIconPath: '/static/images/intervene-active.png',
          text: '干预'
        },
        {
          pagePath: '/pages/community/index',
          iconPath: '/static/images/community.png',
          selectedIconPath: '/static/images/community-active.png',
          text: '社区'
        },
        {
          pagePath: '/pages/user/home',
          iconPath: '/static/images/user.png',
          selectedIconPath: '/static/images/user-active.png',
          text: '我的'
        }
      ]
    };
  },
  mounted() {
    // 获取当前页面路径
    const pages = getCurrentPages();
    if (pages.length > 0) {
      const currentPage = pages[pages.length - 1];
      const route = '/' + currentPage.route;
      this.currentTab = route;
    }
  },
  methods: {
    switchTab(path) {
      if (this.currentTab === path) return;
      
      this.currentTab = path;
      uni.switchTab({
        url: path
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.custom-tab-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100rpx;
  background-color: #ffffff;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0 -2rpx 10rpx rgba(0, 0, 0, 0.05);
  z-index: 999;
  
  // 适配底部安全区域
  padding-bottom: env(safe-area-inset-bottom);
  
  .tab-item {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    
    &.active {
      .tab-text {
        color: #6A5ACD;
      }
    }
    
    .tab-icon {
      width: 50rpx;
      height: 50rpx;
      display: flex;
      justify-content: center;
      align-items: center;
      
      image {
        width: 40rpx;
        height: 40rpx;
      }
    }
    
    .tab-text {
      font-size: 22rpx;
      margin-top: 6rpx;
      color: #7A7E83;
    }
  }
}
</style> 